.card {
  background: #fff;
  border-radius: 2px;
  padding: 15px;
  height: 300px;
  margin: 1rem;
  position: relative;
  min-width: 350px;
  max-width: 350px;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  transform: scale(.98);
  transition: .3s;
}

.card:hover {
  transform: scale(1);
}

.reverse {
  transform: rotate(135deg) !important;
  top: 35px !important;
}

.arrow {
  color: #fff;
  position : absolute; 
  bottom : 0px;
  width : 100%; 
  left : 0px; 
  height : 70px;
  z-index: 999;
}
.arrow span {
  position: absolute;
  top: 20px;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  box-sizing: border-box;
}



.left-arrow {
  color: gray;
  position: absolute;
  left: 0px;
  height: 70px;
  z-index: 999;
  width: 70px;
  transform: rotate(90deg);
  cursor: pointer;
}
.left-arrow span {
  position: absolute;
  top: 20px;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 3px solid gray;
  border-bottom: 3px solid gray;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  box-sizing: border-box;
}


.right-arrow {
  color: gray;
  position: absolute;
  right : 0px;
  height: 70px;
  z-index: 999;
  width: 70px;
  transform: rotate(-90deg);
  cursor: pointer;
}
.right-arrow span {
  position: absolute;
  top: 20px;
  left: 50%;
  width: 24px;
  height: 24px;
  margin-left: -12px;
  border-left: 3px solid gray;
  border-bottom: 3px solid gray;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  box-sizing: border-box;
}

